.nocomments {
  display: none; }
  .comment-number {
   margin: 30px 0 20px 10px; }

   .comment-number h4 {
    display: inline;
    font-size: 2.2rem;
    font-weight: normal;
    opacity: .7;
  }
  .comment-number span a,
  .comment-number span a:visited {
    margin: 0 10px 0 20px;
    padding: 8px 16px;
    font-size: 1.2rem;
    color: $color_link_comenk;
    font-weight: bold;
    border-radius: 16px;
    background:$top_line_color;
    text-decoration: none;
    @include trans;
  }
  .comment-number span a:hover {
    background: darken($top_line_color, 10%);
  }

  .c-head  {
    font-size: 1.3rem;
    font-weight: bold;
    padding: 10px 20px;
    margin-bottom: 15px;
  // margin: 0 10px 10px 10px;
  background: #cfd3d5;
  text-align: right;
}

.c-head span {
  font-size: .9rem;
  color:$color_link_comenk;
  font-weight: normal;
  margin-left: .3rem;
}
.c-head a,
.c-head a:visited {
  color: $color_link_comenk;
  text-decoration: none;
  border-bottom: 1px dotted $color_border;
  @include trans;
}
.c-head a:hover {
  color: #000;
  border-bottom: 1px solid;
  text-decoration: none;
}

.comment-box {
  background: #fff;
  margin: 5px 0;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  border: 1px solid $color_border;
}

.commentlist {
	text-align: right;
  list-style: none;
}
.commentlist ul.children,
#comments ul.children {
	width: 90%;
  display: inline-block;
  list-style: none;
}
.comment-author-construction .c-head {
  background:#ffcb0f;
}

.comment-content-box {
  display: flex;
  justify-content: space-between;
}
.c-grav {
  width: 58px;
  height: 58px;
  margin-right: 20px;
}

.c-grav img {
  border: 4px solid $top_line_color;
  border-radius: 4px;
}
.c-body {
  padding-bottom: 10px;
  text-align: right;
}
.c-body p {
	margin-bottom: 10px;
  font-size: 1.3rem;
  text-align: left;
}

.reply {
 padding: 10px 0;
 display: inline-block;
}

.reply a,
.reply a:visited {
  color:$color_link_comenk;
  font-size: .9rem;
  text-transform: lowercase;
  border-bottom: 1px dotted  $color_border;
  text-decoration: none;
  @include trans;
}

.reply a:hover {
  border-bottom: 1px solid;
  text-decoration: none;
  color: #13293e;
}

.navigation {
	display: block;
	margin-bottom: 10px;
	display: flex;
 justify-content: space-between;
}

.alignleft { display: inline-block;  }
.alignright {  display: inline-block;}

#respond {
 background: #fff;
 margin: 5px 0;
 border: 1px solid $color_border;
}
#postcomment {
 margin: 10px;
 padding: 10px 20px 10px 0;
 font-size: 1.3rem;
 color: $color_link_comenk;
 background: $color_border;
 text-align: right;
}

#respond a {
  color: #000;
}
#respond .cancel-comment-reply {
  margin-left: 10px;
}

form#commentform, p.logged-in {
  margin: 30px;
  font-size: 1.20rem;
}
p.logged-in a, .bloged-in a {
 color: $color_link_comenk;
 text-decoration: underline;
}

.bloged-in {
  font-size: 1.20rem;
}
#commentform .header_button {
  border:none;
}

.commenting_input {
  display: block;
  width: 100%;
  height: 40px;
  text-indent: 10px;
  border: 1px solid $color_border;
  color: $color_link_comenk;
  outline: none;
  @include trans;
  background-color: #ffffff !important;
}

.commenting_textarea {
  display: block;
  width: 100%;
  height: 140px;
  resize: vertical;
  text-indent: 10px;
  margin-bottom: 20px;
  padding-top: 10px;
  border: 1px solid $color_border;
  color: $color_link_comenk;
  outline: none;
  @include trans;
}

label.comment-field {
  display: block;
  margin: 10px 0 5px 0;
  color: $color_link_comenk;
}

#commentform .mainbuttons {
  border:none;
}

#commentform .commenting_input:-webkit-autofill {    // важно
-webkit-box-shadow: inset 0 0 0 70px #ffffff !important; /* Цвет фона */
-webkit-text-fill-color: #aaaaaa !important; /* цвет текста */
 color: $color_link_comenk !important; /* цвет текста */
 background-color: #fff;
}

#commentform .commenting_input:focus-within {
  background-color: #ffffff !important;
  color: $color_link_comenk;
}
.form_input:hover {
  background-color: #ffffff !important;
  color: $color_link_comenk;
}
.form_input:focus {
  background-color: #ffffff !important;
  color: $color_link_comenk;
}
.form_input:focus-within {
  background-color: #ffffff !important;
  color: $color_link_comenk;
}

.form_input:visited {
  background-color: red !important;
  color: $color_link_comenk;
}












